.page {
  font-family: "Manrope", sans-serif;
  font-weight: 400;

  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;

  background-color: #fff;
  scroll-behavior: smooth;

  &:has(.page__body--with-menu) {
    overflow: hidden;
  }

  &__body {
    margin: 0 auto;
    min-width: 0 auto;
    max-width: 1440px;
  }

  &__menu {
    z-index: 2;

    position: fixed;
    top: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform $duration ease-in-out;

    &:target {
      transform: translateX(0);
    }
  }
}
